import React, { useEffect, useState, Suspense, lazy } from 'react';
import Loading from './components/Loading';
import './components/App.css';

// Lazy load components
const Navbar = lazy(() => import('./components/Navbar'));
const About = lazy(() => import('./components/About'));
const SecondaryNavbar = lazy(() => import('./components/SecondaryNavbar'));
const Footer = lazy(() => import('./components/Footer'));
const SuperBadassBar = lazy(() => import('./components/SuperBadassBar'));
const RenderBanner = lazy(() => import('./components/RenderBanner'));
const CatchMe = lazy(() => import('./components/CatchMe'));
const CatchMobile = lazy(() => import('./components/CatchMobile'));
const Projects = lazy(() => import('./components/Projects'));
const ArchiveContainer = lazy(() => import('./components/ArchiveContainer'));
const Testimonials = lazy(() => import('./components/Testimonials'));
const CardContainer = lazy(() => import('./components/CardContainer'));
const OneMoreThink = lazy(() => import('./components/OneMoreThink'));
const AutoScrollBar = lazy(() => import('./components/AutoScrollBar'));
const WebflowBanner = lazy(() => import('./components/WebflowBanner'));

function App() {
  const [isMobile, setIsMobile] = useState(window.innerWidth <= 768);
  const [showNavbar, setShowNavbar] = useState(false);
  const [loading, setLoading] = useState(true); // Loading state
  const [loadingTimeout] = useState(2000); // Set your desired timeout duration (in milliseconds)

  useEffect(() => {
    // Set a timer for the loading screen
    const timer = setTimeout(() => {
      setLoading(false); // Hide loading after the timeout
    }, loadingTimeout);

    // Check if the components are loaded
    const handleLoad = () => {
      setLoading(false);
      clearTimeout(timer); // Clear the timeout if components load before the timeout
    };

    // Add an event listener to check if the window is loaded
    window.addEventListener('load', handleLoad);

    return () => {
      clearTimeout(timer);
      window.removeEventListener('load', handleLoad);
    };
  }, [loadingTimeout]);

  useEffect(() => {
    const savedPosition = localStorage.getItem('scrollPosition');
    if (savedPosition) {
      window.scrollTo(0, parseInt(savedPosition, 10));
    }

    const handleBeforeUnload = () => {
      localStorage.setItem('scrollPosition', window.scrollY);
    };

    const handleScroll = () => {
      const bannerHeight = document.getElementById('banner').offsetHeight;
      const footerTop = document.getElementById('footer').offsetTop;
      const scrollY = window.scrollY;
      setShowNavbar(scrollY > bannerHeight && scrollY < footerTop);
    };

    window.addEventListener('beforeunload', handleBeforeUnload);
    window.addEventListener('scroll', handleScroll);
    window.addEventListener('resize', () => setIsMobile(window.innerWidth <= 768));

    return () => {
      window.removeEventListener('beforeunload', handleBeforeUnload);
      window.removeEventListener('scroll', handleScroll);
      window.removeEventListener('resize', () => setIsMobile(window.innerWidth <= 768));
    };
  }, []);

  // Show loading screen while loading
  if (loading) {
    return <Loading />;
  }

  return (
    <div style={{ minHeight: '150vh', display: 'flex', flexDirection: 'column' }}>
      <Suspense fallback={<Loading />}>
        <SecondaryNavbar />
        <Navbar show={showNavbar} />
        <div className="content">
          <div id="banner">
            <RenderBanner />
          </div>
          <div id="about">
            <About />
          </div>
          <SuperBadassBar />
          <Projects />
          <div id="work">
            <ArchiveContainer />
          </div>
          <OneMoreThink />
          <CardContainer />
          <div id="contact">
            <WebflowBanner />
            {isMobile ? <CatchMobile /> : <CatchMe />}
          </div>
          <AutoScrollBar />
          <div id="testimonials">
            <Testimonials />
          </div>
        </div>
        <div id="footer">
          <Footer />
        </div>
      </Suspense>
    </div>
  );
}

export default App;
